import React, { useState, useEffect } from "react";
import {
  collection,
  getDocs,
  query as firestoreQuery,
  where,
  updateDoc,
  doc,
} from "firebase/firestore";
import { db, auth } from "../firebaseConfig";
import { signOut } from "firebase/auth";
import { graphql, navigate } from "gatsby";
import { useTranslation } from "gatsby-plugin-react-i18next";

interface User {
  id: string;
  name: string;
  email: string;
  phone?: string;
  address?: string;
  postalCode?: string;
  country?: string;
}

const AdminPanel = () => {
  const [users, setUsers] = useState<User[]>([]);
  const { t } = useTranslation();
  useEffect(() => {
    const fetchUsers = async () => {
      const q = firestoreQuery(
        collection(db, "users"),
        where("isApproved", "==", false)
      );
      const querySnapshot = await getDocs(q);
      const usersData = querySnapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      })) as User[];
      setUsers(usersData);
    };
    fetchUsers();
  }, []);

  const approveUser = async (userId: string) => {
    const userDoc = doc(db, "users", userId);
    await updateDoc(userDoc, { isApproved: true });
    setUsers(users.filter((user) => user.id !== userId));
  };

  const handleLogout = async () => {
    try {
      await signOut(auth);
      navigate("/" as unknown as number);
    } catch (error) {
      console.error("Error al cerrar sesión:", error);
      alert("Error al cerrar sesión. Intenta nuevamente.");
    }
  };

  return (
    <div className="min-h-screen bg-secondary flex flex-col items-center p-6">
      <div className="w-full max-w-4xl bg-dark shadow-lg rounded-lg p-6">
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-2xl font-bold text-light">{t("approveTitle")}</h2>
          <button
            onClick={handleLogout}
            className="px-4 py-2 bg-red-500 text-white rounded-lg shadow hover:bg-red-600 transition"
          >
            {t("logout")}
          </button>
        </div>
        <ul className="space-y-4">
          {users.map((user) => (
            <li
              key={user.id}
              className="flex justify-between items-center bg-light p-4 rounded-lg shadow-md"
            >
              <div>
                <p className="text-lg font-medium text-dark">{user.name}</p>
                <p className="text-sm text-dark">{user.email}</p>
                <p className="text-sm text-dark">{user.phone}</p>
                <p className="text-sm text-dark">{user.address}</p>
                <p className="text-sm text-dark">{user.postalCode}</p>
                <p className="text-sm text-dark">{user.country}</p>
              </div>
              <button
                onClick={() => approveUser(user.id)}
                className="px-4 py-2 bg-dark text-light rounded-lg shadow hover:bg-secondary hover:text-dark transition"
              >
                {t("approve")}
              </button>
            </li>
          ))}
        </ul>
        {users.length === 0 && (
          <p className="text-center text-gray-600 mt-6">{t("approveMessage")}</p>
        )}
      </div>
    </div>
  );
};

export default AdminPanel;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;